import React from 'react';
import { Link } from 'react-router-dom';

function Header(props) {

    function handleCreate() {
        props.setShape(1);
        props.setBladeColor(1);
        props.setName('');
        props.setDecoration(1);
        props.setHilt(0);
        props.setPower(0);
    }

    return(
        <div className="container__top">
                <ul className="navbar">
                    <li className="navbar__item"><Link className="navbar__link" to="/">Home</Link></li>
                    <li className="navbar__item"><Link className="navbar__link" to="/Gallery">Gallery</Link></li>
                    <li className="navbar__item"><Link className="navbar__link" to="/create" onClick={handleCreate}>Create</Link></li>
                </ul>
        </div>
    );

}

export default Header;